body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

a {
  color: inherit;
  text-decoration: none;
}

button.floatingBtn {
  margin: '10px';
  z-index: 999;
  position: 'fixed';
  bottom: 0;
  right: 0;
}

.gridFloatingBtn {
  display: grid;
  grid-template-columns: 50px 50px;
  grid-gap: 10px
}

div.searchName {
  margin-left: 10px;
}


